.widgets {
  flex: 0.3;
  min-width: fit-content;
  overflow-y: scroll;
}

.widgets::-webkit-scrollbar {
  display: none;
}

.widgets__hamburgerIcon{
  visibility: hidden;
}

.widgets__input {
  display: flex;
  align-items: center;
  background-color: var(--twitter-background);
  padding: 10px 20px;
  border-radius: 50px;
  margin-top: 10px;
  margin-left: 20px;
}

.widgets__input>input {
  border: none;
  margin-top: 0px;
  background-color: var(--twitter-background);
}

.widgets__searchIcon {
  color: gray;
}

.widgets__widgetContainer {
  margin-top: 15px;
  margin-left: 20px;
  padding: 20px;
  background-color: #f5f8fa;
  border-radius: 20px;
}

.widgets__widgetContainer>h2 {
  font-size: 18px;
  font-weight: 800;
}

@media screen and (max-width: 768px) {
  .widgets {
    display: none;
  }

  .widgets__hamburgerIcon{
    visibility: visible;
  }

  .widgets__hamburger {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    background-color: white;
    width: 250px;
    height: 100%;
    z-index: 1000;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
  }

  .widgets__hamburger.open {
    transform: translateX(0);
  }

  .widgets__hamburgerIcon {
    display: block;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 1100;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
  }

  .widgets__hamburgerIcon.open {
    transform: translateX(-250px);
  }
}